import React from "react";
import PostContent from "./PostContent";
import { Container, Col, Row, Button } from "react-bootstrap";
import { Link, Switch, Route, useRouteMatch } from "react-router-dom";
import styles from "./CustomCard.module.css";
import ChildComponent from './ChildComponent';
function Posts(props) {
  const { path, url } = useRouteMatch();

  return (
    <React.Fragment>
      <Switch>
      <Route path={`${url}/childcomponent`}>
          <ChildComponent />
        </Route>
        <Route path={`${path}/:postID`}>
          <PostContent posts={props.Posts} />
        </Route>
       

        <Route path={`${path}/`}>
          <Container>
            <Row className="justify-content-center">
              <Col className="mt-3" lg={6}>
                <div
                className={[
                  "card border-dark",
                  styles.myCard,
              ].join(" ")}
                >
                  <div
                  className={[
                    "card-header bg-dark text-white text-center",
                    styles.cardHeader,
                ].join(" ")}
                  >
                    <b>Posts</b>
                  </div>
                  <ul className="list-group list-group-flush">
                    {props.Posts.map((Post, index) => (
                      <li
                        key={Post.id}
                        className={["list-group-item",styles.listGroupItem].join(' ')}
                      >
                        <div className="row justify-content-between">
                          <label className="col-12 col-md-6 text-left pl-3">
                            <b>
                              Post {Post.id}: {Post.title}
                            </b>
                          </label>
                          <Link
                            to={`${path}/${Post.id}`}
                            className="text-white text-decoration-none"
                          >
                            <Button
                              variant="outline-primary"
                              style={{
                                borderRadius: "25px",
                              }}
                            >
                              View Post
                            </Button>
                          </Link>
                        </div>
                      </li>
                    ))}
                    <li
                    className={["list-group-item",styles.listGroupItem].join(' ')}
                      >
                        <div className="row justify-content-center">
                          <Link
                            to={`${url}/childcomponent`}
                            className="text-white text-decoration-none"
                          >
                            <Button
                              variant="outline-primary"
                              className={styles.roundedButton}
                            >
                              Child Component
                            </Button>
                          </Link>
                        </div>
                      </li>
                  </ul>
                </div>
              </Col>
            </Row>
          </Container>
        </Route>
      </Switch>
    </React.Fragment>
  );
}

export default Posts;
