import React from "react";
import { Col } from "react-bootstrap";
import styles from "./CustomCard.module.css";
const Sidebar = () => {
  return (
    <Col sm={4} className="mt-3">
      <div
      className={[
        "card border-warning",
        styles.myCard,
    ].join(" ")}
      >
        <b 
        className={[
          "card-header bg-dark text-white text-center",
          styles.cardHeader,
      ].join(" ")}>Sidebar</b>
        <ul className="list-group list-group-flush">
          <li className="list-group-item">
            This component is only visible to Laptop and Desktop
          </li>
          <li className="list-group-item">Dummy Text</li>
          <li className={["list-group-item",styles.listGroupItem].join(' ')}>Notifications</li>
        </ul>
      </div>
    </Col>
  );
};

export default Sidebar;
