import React from "react";
import { Col, Card, Row, Button } from "react-bootstrap";
import { Link, useParams } from "react-router-dom";
import styles from "./CustomCard.module.css";
function PostContent(props) {
  let { postID } = useParams();

  return (
    <React.Fragment>
      <Row className="justify-content-center">
        <Col className="mt-3" lg={7}>
          <div
          className={[
            "card bg-light border-dark",
            styles.myCard,
        ].join(" ")}
          >
            <div
            className={[
              "card-header bg-dark text-white text-center",
              styles.cardHeader,
          ].join(" ")}
            >
              <h4>Post {props.posts[postID - 1].id}</h4>
            </div>
            <Card className={styles.listGroupItem}
            >
              <Card.Body>
                <Card.Title className="text-center">
                  {props.posts[postID - 1].title}
                </Card.Title>
                <Card.Text>{props.posts[postID - 1].content}</Card.Text>
                <Link to="/posts">
                  <Button
                    variant="outline-primary"
                    className={styles.roundedButton}
                  >
                    Back
                  </Button>
                </Link>
              </Card.Body>
            </Card>
          </div>
        </Col>
      </Row>
    </React.Fragment>
  );
}

export default PostContent;
