import React, { Component } from "react";
import { Container, Col, Row } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import styles from "./CustomCard.module.css";

class Users extends Component {
  render() {
    return (
      <React.Fragment>
        <Container>
          <Row className="justify-content-center">
            <Col className="mt-3" lg={6}>
              <div
              className={[
                "card border-dark",
                styles.myCard
            ].join(" ")}
              >
                <div
                className={[
                    "card-header bg-dark text-white text-center",
                    styles.cardHeader
                ].join(" ")}
                >
                  <b>User List</b>
                </div>
                <ul className="list-group list-group-flush">
                  {this.props.Users.map((User, index) => (
                    <li
                      key={User.id}
                      className={["list-group-item",styles.listGroupItem].join(' ')}
                    >
                      <div className="row text-left">
                        <label className="col-12 col-sm-6 text-left pl-3">
                          <b>
                            {User.id}. {User.FirstName} {User.LastName}
                          </b>
                        </label>
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
            </Col>
            <Col className="mt-3" lg={6}>
              <UrlQuery />
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function UrlQuery() {
  const QueryParams = useQuery();
  return (
    <div
    className={[
        "card border-dark",
        styles.myCard,
    ].join(" ")}
    >
      <div
      className={[
        "card-header bg-dark text-white text-center",
        styles.cardHeader,
    ].join(" ")}
      >
        <b>URL Query</b>
      </div>
      <ul className="list-group list-group-flush">
        <li className={["list-group-item",styles.listGroupItem].join(' ')}>
          <label className="col-12 col-sm-6 text-left pl-3">
            <b>pageno : {QueryParams.get("pageno") || 1} </b>
          </label>
        </li>
        <li className={["list-group-item",styles.listGroupItem].join(' ')}>
          <label className="col-12 col-sm-6 text-left pl-3">
            <b>Sort : {QueryParams.get("sort") || "id"} </b>
          </label>
        </li>
        <li className={["list-group-item",styles.listGroupItem].join(' ')}>
          <label className="col-12 col-sm-6 text-left pl-3">
            <b>page size : {QueryParams.get("pagesize") || 10} </b>
          </label>
        </li>
      </ul>
    </div>
  );
}

export default Users;
